/* Global Colours */
:root {
  --green: #6aaa64;
  --darkenedGreen: #538d4e;
  --yellow: #c9b458;
  --darkenedYellow: #b59f3b;
  --lightGrey: #d8d8d8;
  --grey: #86888a;
  --darkGrey: #939598;
  --white: #ffffff;
  --black: #212121;
  --paleRed: #f5c6cb;
  --darkRed: #721c24;
  /* Colour blind colours */
  --orange: #f5793a;
  --blue: #85c0f9;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-y: hidden;
  user-select: none;
}

.loader {
  display: block;
  margin: auto;
  margin-top: min(10vw, 8vh);
  border: min(16px, min(6vw, 6vh)) solid var(--lightGrey);
  border-top: min(16px, min(6vw, 6vh)) solid var(--darkGrey);
  border-radius: 50%;
  width: min(120px, min(50vw, 50vh));
  height: min(120px, min(50vw, 50vh));
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.game {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid var(--lightGrey);
}

.title {
  margin-top: 2vh;
  margin-bottom: 2vh;
  font-size: 1.7rem;
  font-weight: 700;
  letter-spacing: 0.2rem;
  text-transform: uppercase;
  text-align: center;
}

@media (max-width: 600px) {
  .title {
    letter-spacing: 0.1rem;
    font-size: min(calc((100vw - min(45vw, 200px)) / 13), 1.7rem);
  }
}

.give-up {
  cursor: pointer;
  position: absolute;
  top: 2vh;
  left: calc((100% - 600px) / 2 + 15px);
  border-radius: 5px;
  background-color: var(--orange);
  color: var(--white);
  width: min(14vw, 70px);
  height: min(6vw, 30px);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: min(3vw, 1rem);
}

.settings {
  cursor: pointer;
  position: absolute;
  top: 2vh;
  right: calc((100% - 600px) / 2 + 15px);
  fill: var(--darkGrey);
  width: min(5vw, 30px);
  height: min(5vw, 30px);
  display: flex;
  justify-content: center;
  align-items: center;
}

.settings:hover,
.settings:focus {
  fill: var(--black);
}

.report {
  cursor: pointer;
  position: absolute;
  top: 2vh;
  right: calc((100% - 600px) / 2 + 55px);
  width: min(5vw, 30px);
  height: min(5vw, 30px);
  display: flex;
  justify-content: center;
  align-items: center;
  stroke-width: 2;
  fill: none;
  stroke: var(--darkGrey);
  stroke-linejoin: round;
  stroke-linecap: round;
}

.report:hover,
.report:focus {
  stroke: var(--black);
}

.dot {
  stroke-width: 1;
  fill: var(--darkGrey);
}

.dot:hover,
.dot:focus {
  fill: var(--black);
}

@media (max-width: 600px) {
  .give-up {
    left: 2.6vw;
  }

  .settings {
    right: 2.6vw;
  }

  .report {
    right: 10vw;
  }
}

.failed-card {
  width: 100%;
  max-width: 600px;
  height: 35vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-radius: 10px;
  background-color: var(--paleRed);
  color: var(--darkRed);
  font-size: min(1.5rem, 4vw);
}

.game-area {
  --word-length: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: min(4vw, 3vh);
}

.word-area {
  --area-size: min(16vw, 8vh);
  width: calc(var(--area-size) * 5);
  height: calc(var(--area-size) * 4.4);
  max-width: 350px;
  max-height: 310px;
  display: grid;
  grid-gap: min(min(1vw, 0.5vh), 5px);
}

.letter-row {
  width: min(100%, calc(100% * var(--word-length) / 5));
  padding-left: max(0%, calc(100% * (5 - var(--word-length)) / 10));
  display: grid;
  grid-template-columns: repeat(var(--word-length), 1fr);
  grid-gap: min(min(1vw, 0.5vh), 5px);
}

.answer-row {
  margin-top: min(min(5.5vw, 2.7vh), 32px);;
}

.answer-row[data-animation="shake"] {
  animation-name: Shake;
  animation-duration: 600ms;
}

.square {
  width: min(100%, min(16vw, 8vh));
  height: min(16vw, 8vh);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: calc(min(min(8vw, 4vh), 2rem) * min(5 / var(--word-length), 1));
  box-sizing: border-box;
  font-weight: bold;
  padding: 0;
  margin: 0;
  color: var(--white);
  background-color: var(--white);
  text-transform: uppercase;
}

.square[data-state="green"] {
  background-color: var(--green);
}

.square[data-state="yellow"] {
  background-color: var(--yellow);
}

.square[data-state="grey"] {
  background-color: var(--grey);
}

.square[data-state="empty"] {
  color: var(--black);
  padding-bottom: calc(100% - 2px * 2);
  border: 2px solid var(--lightGrey);
}

.square[data-state="input"] {
  color: var(--black);
  border: 2px solid var(--darkGrey);
  animation-name: PopIn;
  animation-duration: 100ms;
}

.square[data-animation='flip-in'] {
  animation-name: FlipIn;
  animation-duration: 250ms;
  animation-timing-function: ease-in;
}

.square[data-animation='flip-out'] {
  animation-name: FlipOut;
  animation-duration: 250ms;
  animation-timing-function: ease-in;
}

.square[data-animation='bounce'] {
  animation-name: Bounce;
  animation-duration: 1000ms;
}

.keyboard {
  display: flex;
  flex-direction: column;
  position: fixed;
  bottom: min(7vh, 30px);
}

.key-row {
  --num-keys: 0;
  display: flex;
  justify-content: center;
  margin-top: min(7px, 1vh);
}

.key {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  background-color: var(--lightGrey);
  width: min(45px, min(8vw, 10vh));
  height: min(60px, 10vh);
  margin-right: min(7px, min(1vw, 1vh));
  font-size: 0.8rem;
  font-weight: bold;
}

.key[data-width="wide"] {
  width: min(72px, max(13vw, 50px));
}

.key[data-colour="green"] {
  background-color: var(--green);
  color: var(--white);
}

.key[data-colour="yellow"] {
  background-color: var(--yellow);
  color: var(--white);
}

.key[data-colour="darkGrey"] {
  background-color: var(--darkGrey);
  color: var(--white);
}

.report-container {
  display: none;
  justify-content: center;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0,0,0);
  background-color: rgba(0,0,0,0.2);
}

.report-container[data-animation='in'] {
  animation-name: FadeIn;
  animation-duration: 400ms;
}

.report-container[data-animation='out'] {
  animation-name: FadeOut;
  animation-duration: 400ms;
}

.report-modal {
  position: relative;
  background-color: var(--white);
  top: min(200px, 30vh);
  width: min(90%, 540px);
  height: 180px;
  border-radius: 20px;
}

.report-modal[data-animation='in'] {
  animation-name: MoveUp;
  animation-duration: 400ms;
}

.report-modal[data-animation='out'] {
  animation-name: MoveDown;
  animation-duration: 400ms;
}

.report-content {
  display: block;
  padding: 20px 20px;
}

.report-title {
  padding-bottom: 10px;
}

.checkbox-container {
  padding: 4px 0;
}

.report-submit {
  background-color: var(--green);
  border: none;
  border-radius: 5px;
  color: var(--white);
  padding: 10px 30px;
  text-align: center;
  position: absolute;
  right: 20px;
  bottom: 20px;
  font-size: 16px;
}

.settings-menu {
  display: none;
  position: fixed;
  z-index: 1;
  top: 0;
  width: min(100%, 600px);
  height: 100vh;
  background-color: var(--white);
}

.settings-menu[data-animation='in'] {
  animation-name: FadeInTop;
  animation-duration: 400ms;
}

.settings-menu[data-animation='out'] {
  animation-name: FadeOutTop;
  animation-duration: 400ms;
}

.settings-title {
  padding-top: min(8vw, 3vh);
  font-size: min(5vw, 1rem);
  font-weight: 550;
  letter-spacing: 0.05rem;
  text-transform: uppercase;
  text-align: center;
}

.close {
  position: absolute;
  color: var(--darkGrey);
  width: min(5vw, 30px);
  top: 2vh;
  right: 1vw;
  font-size: min(28px, 5vw);
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: #000;
  cursor: pointer;
}

.settings-body {
  padding: 2px 16px;
}

.settings-footer {
  position: absolute;
  bottom: 0;
  padding: 16px 16px;
  width: calc(100% - 32px);
  text-align: right;
  font-size: 0.5rem;
}

.setting-box {
  margin-top: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid var(--lightGrey);
  font-size: min(1rem, 6vw);
}

.slider {
  position: relative;
  -webkit-appearance: none;
  width: 50%;
  height: 15px;
  float:right;
  border-radius: 5px;  
  background: var(--lightGrey);
  outline: none;
}

@media  (max-width: 420px) {
  .slider {
    display: block;
    float:none;
    width: 100%;
    margin-top: 10px;
  }
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none; 
  appearance: none;
  width: 25px; 
  height: 25px;
  background: var(--green);
  border-radius: 50%;
  cursor: pointer; 
}

@keyframes FadeIn {
  from {opacity: 0}
  to {opacity: 1}
}

@keyframes FadeOut {
  from {opacity: 1}
  to {opacity: 0}
}

@keyframes MoveUp {
  from {top: min(300px, 40vh);; opacity:0}
  to {top: min(200px, 30vh); opacity:1}
}

@keyframes MoveDown {
  from {top: min(200px, 30vh); opacity:1}
  to {top: min(300px, 40vh); opacity:0}
}

@keyframes FadeInTop {
  from {top: 100px; opacity:0}
  to {top:0; opacity:1}
}

@keyframes FadeOutTop {
  from {top: 0; opacity:1}
  to {top:100px; opacity:0}
}

@keyframes PopIn {
  from {
    transform: scale(0.8);
    opacity: 0;
  }

  40% {
    transform: scale(1.1);
    opacity: 1;
  }
}

@keyframes FlipIn {
  0% {
    transform: rotateX(0);
  }
  100% {
    transform: rotateX(-90deg);
  }
}

@keyframes FlipOut {
  0% {
    transform: rotateX(-90deg);
  }
  100% {
    transform: rotateX(0);
  }
}

@keyframes Shake {
  10%,
  90% {
    transform: translateX(-1px);
  }

  20%,
  80% {
    transform: translateX(2px);
  }

  30%,
  50%,
  70% {
    transform: translateX(-4px);
  }

  40%,
  60% {
    transform: translateX(4px);
  }
}

@keyframes Bounce {
  0%, 20% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  50% {
    transform: translateY(5px);
  }
  60% {
    transform: translateY(-15px);
  }
  80% {
    transform: translateY(2px);
  }
  100% {
    transform: translateY(0);
  }
}